<template>
  <div class="header">
    <div class="logo"></div>
    <div class="top-nav">
      <ul class="top-nav__list" ref="topNav">
        <!-- <li class="nav-item" v-for="(item, i) in topNavs" :key="item.id" :class="{selected:select == i}" @click="clickLi(i)">
          {{item.name}}
        </li> -->

        <router-link tag="li" class="tab-item" to="/home">
          <a>我的首页</a>
        </router-link>
        <router-link tag="li" class="tab-item" to="/knowledge">
          <a>知识库</a>
        </router-link>
        <router-link tag="li" class="tab-item" to="/taskSystem">
          <a>项目管理</a>
        </router-link>
        <li class="tab-item">
          <a>工作台</a>
        </li>
        <li class="tab-item">
          <a>产品资料</a>
        </li>
        <li class="tab-item">
          <a>系统管理</a>
        </li>
         <router-link tag="li" class="tab-item" to="/personalManagement">
          <a>合伙人管理</a>
        </router-link>
        <li class="tab-item">
          <a>云环境</a>
        </li>
        <!-- <router-link tag="li" class="tab-item" to="/qu-1">
          <a>调查问卷</a>
        </router-link> -->
      </ul>
    </div>
    <div class="right-options">
      欢迎您 <span @click = 'toUserManagement' class="user-name">[{{userName}}]</span>
      <span class="logout" @click="logout">登出</span>
    </div>
  </div>
</template>
<script>
export default {
  name: 'Header',
  data () {
    return {
      activeName: 'second',
      userName: '吴超亭',
      select: 0
    }
  },
  activated () {},
  mounted () {
  },
  methods: {
    handleClick (tab, event) {
      console.log(tab, event)
    },
    clickLi (index) {
      this.select = index
    },
    logout () {
      localStorage.setItem('token', '')
      this.$router.push({ path: '/login' })
    },
    toUserManagement () {
      this.$router.push({ path: '/userManagement' })
    }
  }
}

</script>
<style scoped lang="scss">
  $baseHeight:60px;

  .header {
    height: 60px;
    position: relative;

    .logo {
      float: left;
      width: 240px;
      height: 100%;
      background: url('~@assets/imgs/logo.png') left center no-repeat; //~@assets 是在webpack.base.conf.配置的别名
      background-size: 100% auto;
    }

    .top-nav {
      position: absolute;
      width:80%;
      height: 100%;
      left: 50%;
      transform: translateX(-50%);
      display: flex;
      justify-content: center;
      align-items: center;
      color: #fff;
      font-size: 14px;

      li {
        width: 70px;
        float: left;
        text-align: center;
        margin-right: 30px;
        position: relative;
        cursor: pointer;
        a {
          color:#fff;
          text-decoration: none;
        }
        &.router-link-exact-active {
          color: #fff;

          &:after {
            content: '';
            position: absolute;
            left: 0;
            bottom: -10px;
            width: 100%;
            height: 2px;
            background-color: #fff;
          }
        }
      }
    }
  }
  .right-options {
      float: right;
      color: #fff;
      line-height: $baseHeight;
      .user-name {
        cursor: pointer;
      }
  }
  .logout {
    margin-left:20px;
    cursor: pointer;
  }
</style>



